import window from 'global/window';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

import PropTypes from 'prop-types';
import MotorMain from '../../components/Motor/MotorMain';

import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { setLoading, clearLoading } from '../../actions/loadingScreenActions';
import { getMotorFields } from '../../actions/motorActions';
import { fbTag, gaTag, ampTag } from '../../lib/tagFunctions';

class MotorMainContainer extends Component {
  constructor(props) {
    super(props);

    const { loading, motorFields, history } = this.props;

    if (!motorFields) {
      loading(() => history.push('/'));
      this.props.getMotorFields();
    }
  }

  componentDidMount() {
    const { setFormState, history } = this.props;
    setFormState(FORMSTATES.MAIN.index);

    window.scrollTo(0, 0);

    fbTag('PPS2 - Pageview - MotorPage', { Product: 'Motor' });
    gaTag({
      page_title: 'Motor',
      page_path: `/motor${history.location.search}`,
    });
    ampTag('MotorFormS');
  }

  componentDidUpdate(prevProps) {
    const { motorFields, loadingEnd } = this.props;
    if (!prevProps.motorFields && motorFields) {
      loadingEnd();
    }
  }
  render() {
    const { motorFields, motorPromoGiveAway } = this.props;

    if (!motorFields) {
      return null;
    }

    return <MotorMain motorFields={motorFields} promo={motorPromoGiveAway} />;
  }
}

MotorMainContainer.defaultProps = {
  motorFields: null,
  motorPromoGiveAway: null,
};

MotorMainContainer.propTypes = {
  getMotorFields: PropTypes.func.isRequired,
  loading: PropTypes.func.isRequired,
  loadingEnd: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  motorFields: PropTypes.object,
  motorPromoGiveAway: PropTypes.object,
  setFormState: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  motorFields: state.motor.motorFields,
  motorPromoGiveAway: state.insurance.promoGiveaways.motor,
});

const mapDispatchToProps = dispatch => ({
  getMotorFields: () => {
    dispatch(getMotorFields());
  },
  loading: (callBack, errorMsg, time) => {
    dispatch(setLoading(callBack, errorMsg, time));
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

// export default connect(mapStateToProps)(MotorContainer);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MotorMainContainer));
