import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

import Motor1 from 'assets/images/Motor/policypal-car-insurance-1.svg';
import Motor2 from 'assets/images/Motor/policypal-car-insurance-2.svg';
import Motor3 from 'assets/images/Motor/policypal-car-insurance-3.svg';

import MotorEditDetail from './MotorEditDetail';
import FormLanding from '../Templates/FormLanding/FormLanding';


class MotorMain extends Component {
  render() {
    const { motorFields, promo } = this.props;

    return (
      <div className="motor-container">
        <FormLanding
          img1={Motor1}
          img2={Motor2}
          img3={Motor3}
          imgAlt="PolicyPal Car Insurance"
          promo={promo}
          desc={
            <Fragment>
              <p>Get 5% Cashback </p>
              <p>upon purchase of Motor Insurance!</p>
            </Fragment>
          }
        >
          <MotorEditDetail motorFields={motorFields} />
        </FormLanding>
      </div>
    );
  }
}

MotorMain.defaultProps = {
  promo: null,
};

MotorMain.propTypes = {
  motorFields: PropTypes.object.isRequired,
  promo: PropTypes.object,
};

export default MotorMain;
