/**
 * FormLanding.js
 * Wrapper that takes in the main landing form and
 * renders the 3 images with description, along
 * with the promo button
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import window from 'global/window';
import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import PromoPopup from '../PromoPopup/PromoPopup';

import {
  onPageInsurance,
  setStateInsurance,
  onPageMore,
  setStateMore,
  onPageBusiness,
  setStateBusiness,
} from '../../../actions/navActions';

import './FormLanding.scss';

const maxWidthOpaque = 1570;

class FormLanding extends Component {
  constructor(props) {
    super(props);

    // Set Insurance Page Indicator
    const pageState = onPageInsurance.ACTIVE;
    // Reset More Page Indicator
    const resetMore = onPageMore.NONE;

    // Reset Business Page Indicator
    const resetBusiness = onPageBusiness.NONE;

    const {
      setPage,
      resetIndicatorMore,
      resetIndicatorBusiness,
    } = this.props;
    setPage(pageState);
    resetIndicatorMore(resetMore);
    resetIndicatorBusiness(resetBusiness);

    this.state = {
      opacity: 1,
    };

    this.updateOpacity = this.updateOpacity.bind(this);
    this.handlePromoClick = this.handlePromoClick.bind(this);
  }

  componentDidMount() {
    const { promo } = this.props;
    this.updateOpacity();
    window.addEventListener('resize', this.updateOpacity);

    if (promo && promo.main && promo.main.length > 0) {
      this.promoPopup.openPopUp(promo);
    }
  }

  shouldComponentUpdate(nextProps, prevState) {
    const { opacity } = this.state;
    return opacity !== prevState.opacity;
  }

  /**
   * Lifecycle Method - componentWillUnmount
   */
  componentWillUnmount() {
    window.removeEventListener('resize', this.updateOpacity);
  }

  updateOpacity() {
    const newOpacity = window.innerWidth / 1570;
    this.setState({
      opacity: window.innerWidth > maxWidthOpaque || window.innerWidth <= 576 ? 1 : Math.max(0.2, newOpacity * newOpacity * newOpacity),
    });
  }

  handlePromoClick() {
    this.promoPopup.popupRef.openPopup();
  }

  render() {
    const { opacity } = this.state;
    const {
      img1,
      img2,
      img3,
      promo,
      desc,
      imgAlt,
    } = this.props;

    const isPromo = promo && promo.main && promo.main.length > 0;
    const promoOuterStyle = isPromo ? 'promo-outer-extra' : '';
    const promoImgStyle = isPromo ? 'promo-img-extra' : '';

    return (
      <Fragment>
        <Row noGutters>
          <Col className={`landing-form-img-container ${promoImgStyle}`}>
            <img className="img-1 align-self-end" src={img1} alt={`${imgAlt} 1`} />
            <img className="img-2 align-self-end" src={img2} alt={`${imgAlt} 2`} />
            <img className="img-3 align-self-end" src={img3} alt={`${imgAlt} 3`} style={{ opacity }} />
          </Col>
        </Row>
        <Row noGutters>
          <Col>
            <div className={`landing-form-outer-container ${promoOuterStyle}`}>
              {
                isPromo ?
                  <button className="promo-button" onClick={this.handlePromoClick}>
                    {/* <img className="promo" src={img3} alt="Promo.svg" /> */}
                  </button>
                :
                  null
              }
              <div className="desc">
                {desc}
              </div>
              {this.props.children}
            </div>
          </Col>
        </Row>
        <PromoPopup
          ref={node => this.promoPopup = node}
        />
      </Fragment>
    );
  }
}

FormLanding.defaultProps = {
  promo: null,
  setPage: null,
  resetIndicatorMore: null,
  resetIndicatorBusiness: null,
  imgAlt: 'Landing',
};

FormLanding.propTypes = {
  img1: PropTypes.string.isRequired,
  img2: PropTypes.string.isRequired,
  img3: PropTypes.string.isRequired,
  promo: PropTypes.object,
  desc: PropTypes.object.isRequired,
  setPage: PropTypes.func,
  resetIndicatorMore: PropTypes.func,
  resetIndicatorBusiness: PropTypes.func,
  imgAlt: PropTypes.string,
};

const mapDispatchToProps = dispatch => ({
  setPage: (pageState) => {
    dispatch(setStateInsurance(pageState));
  },
  resetIndicatorMore: (resetMore) => {
    dispatch(setStateMore(resetMore));
  },
  resetIndicatorBusiness: (resetBusiness) => {
    dispatch(setStateBusiness(resetBusiness));
  },
});

export default withRouter(connect(null, mapDispatchToProps)(FormLanding));
