/**
 * MotorEditDetail.js
 * Edit Detail View for Motor
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { reduxForm, getFormValues, reset, change } from 'redux-form';
import { withRouter } from 'react-router-dom';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import Car from 'assets/images/MainFormIcons/Car.svg';
import Chest from 'assets/images/MainFormIcons/Chest.svg';

import DropDownV2 from '../Forms/DropDown/DropDownV2';
import Button from '../FormsV3/Button/Button';

import { invokeSubmit } from '../../actions/editFormActions';
import { getMotorPlans } from '../../actions/motorActions';
import { FORMSTATES, setInsuranceFormState } from '../../actions/insuranceActions';

import { validateForm } from '../../lib/validators';
import { fbTag, gaEvent, ampTag } from '../../lib/tagFunctions';

const formName = 'motor';

class MotorEditDetail extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { storeForm, motorFields } = nextProps;
    if (storeForm && storeForm.vehicle_make !== prevState.selectedVehicleMake) {
      const models = motorFields.car_models.options[storeForm.vehicle_make];
      if (models) {
        nextProps.change('vehicle_model', null);
        return ({
          selectedVehicleMake: storeForm.vehicle_make,
          selectedVehicleModels: models,
        });
      }
    }
    return null;
  }

  constructor(props) {
    super(props);

    const { storeForm, motorFields } = this.props;
    this.state = {
      selectedVehicleMake: storeForm ? storeForm.vehicle_make: null,
      selectedVehicleModels: storeForm && storeForm.vehicle_make ? motorFields.car_models.options[storeForm.vehicle_make] : null,
      regYears: _.range(motorFields.reg_year.min, motorFields.reg_year.max + 1, 1),
      errorStringData: {},
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCloseEdit = this.handleCloseEdit.bind(this);
    this.handleRevert = this.handleRevert.bind(this);
    this.validateForm = this.validateForm.bind(this);
  }

  componentDidMount() {
    const { storeForm, setCloseCallBack } = this.props;
    this.originalValues = storeForm;

    if (setCloseCallBack) {
      setCloseCallBack(this.handleRevert);
    }
  }

  shouldComponentUpdate(nextProps, nextState) {
    const { selectedVehicleModels, errorStringData } = this.state;
    const { storeForm } = this.props;

    return selectedVehicleModels !== nextState.selectedVehicleModels ||
            errorStringData !== nextState.errorStringData ||
            storeForm !== nextProps.storeForm;
    /* return true;
    if (nextProps.isRevert) {
      this.handleRevert();
      nextProps.resetRevert();
    }

    return false; */
  }

  validateForm(e) {
    // console.log(e);
    const {
      selectedVehicleModels,
      regYears,
    } = this.state;

    const {
      motorFields,
    } = this.props;

    const errorResult = validateForm(e, [
      'vehicle_make',
      'vehicle_model',
      'year_registered',
    ], {
      vehicle_make: { type: 'ARRAY', data: motorFields.car_brand.options },
      vehicle_model: { type: 'ARRAY', data: selectedVehicleModels },
      year_registered: { type: 'ARRAY', data: regYears },
    });

    this.setState({
      errorStringData: errorResult,
    });

    return errorResult;
  }

  handleSubmit(e) {
    // e.preventDefault();
    // console.log(e);

    const {
      closePopUp,
      setSubmit,
      storeForm,
      setInsurFormState,
      isPopUp,
      changeDetailForm,
      submitCallback,
      history,
    } = this.props;

    this.originalValues = storeForm;

    const errorResult = this.validateForm(e);

    if (_.isEmpty(errorResult)) {
      setSubmit();
      changeDetailForm('vehicle_make', e.vehicle_make);
      changeDetailForm('vehicle_model', e.vehicle_model);
      changeDetailForm('reg_year', e.year_registered);

      if (closePopUp) {
        closePopUp();
      }

      if (submitCallback) {
        submitCallback();
      } else {
        fbTag('PPS2 - ButtonClick - Motor - GetQuote', { Product: 'Motor', Click_Text: 'GetQuote' });
        gaEvent('event', 'Motor - Get Quote', {
          event_category: 'Motor',
          event_label: 'Button - Motor - Get Quote',
        });

        ampTag('MotorGetQuoteA', {
          make: e.vehicle_make,
          model: e.vehicle_model,
          registered: e.year_registered,
        });
      }

      if (!isPopUp) {
        setInsurFormState(FORMSTATES.MORE.index);
        history.push(`/motor${FORMSTATES.MORE.path}`);
      }
    }
  }

  handleRevert() {
    const { resetForm } = this.props;
    resetForm();
    Object.keys(this.originalValues).forEach((key) => {
      this.props.change(key, this.originalValues[key]);
    });
  }

  handleCloseEdit() {
    const { closePopUp } = this.props;
    closePopUp(() => this.handleRevert());
  }

  render() {
    const { selectedVehicleModels, errorStringData, regYears } = this.state;
    const {
      handleSubmit,
      isPopUp,
      form,
      motorFields,
      storeForm,
    } = this.props;

    return (
      <div className="landing-form-container">
        {
          isPopUp ?
            <p className="edit-header">Edit Motor Insurance Details</p>
          :
            null
        }
        <form onSubmit={handleSubmit(this.handleSubmit)}>
          <Row className="input-offset" noGutters>
            <Col className="justify-content-center icon icon-car d-none d-sm-flex" sm={2}>
              <img src={Car} alt="Car.svg" />
            </Col>
            <Col className="input-padding-right" xs={12} sm={5}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="vehicle_make"
                placeholderString="Car brand"
                dropList={motorFields.car_brand.options}
                size="medium"
                zIndex={99}
                numItemsDisplay={7}
                desc={motorFields.car_brand.display_name}
                errorString={errorStringData.vehicle_make}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="input-padding-left" xs={12} sm={5}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="vehicle_model"
                placeholderString="Car Model"
                dropList={selectedVehicleModels}
                size="medium"
                zIndex={98}
                numItemsDisplay={7}
                desc={motorFields.car_models.display_name || ''}
                errorString={errorStringData.vehicle_model}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
          </Row>
          <Row className="align-items-end row-margin" noGutters>
            <Col className="justify-content-center icon icon-chest d-none d-sm-flex" xs={12} sm={2}>
              <img src={Chest} alt="Chest.svg" />
            </Col>
            <Col className="input-padding-right" xs={12} sm={5}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="year_registered"
                placeholderString="Select Year"
                dropList={regYears}
                size="medium"
                zIndex={97}
                numItemsDisplay={7}
                desc={motorFields.reg_year.display_name}
                errorString={errorStringData.year_registered}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
          </Row>
          <Row className="align-items-center button-container" noGutters>
            {
              isPopUp ?
                <Col className="justify-content-center button" xs={12}>
                  <Row noGutters>
                    <Col className="button-container-inner" xs={12} sm={6}>
                      <Button
                        className="button-left"
                        variant="medium-flat"
                        width={165}
                        height={72}
                        onClick={() => this.handleCloseEdit()}
                      >
                        <p>Cancel</p>
                      </Button>
                    </Col>
                    <Col className="button-container-inner" xs={12} sm={6}>
                      <Button
                        type="submit"
                        variant="medium-flat-green"
                        width={165}
                        height={72}
                      >
                        <p>Save</p>
                      </Button>
                    </Col>
                  </Row>
                </Col>
              :
                <Col className="justify-content-center button" xs={12}>
                  <Button type="submit" variant="medium-flat-green" width={165} height={72}>
                    <p>Get Quote</p>
                  </Button>
                </Col>
            }
          </Row>
        </form>
      </div>
    );
  }
}

MotorEditDetail.defaultProps = {
  storeForm: null,
  isPopUp: false,
  closePopUp: null,
  setCloseCallBack: null,
  submitCallback: null,
};

MotorEditDetail.propTypes = {
  setCloseCallBack: PropTypes.func,
  resetForm: PropTypes.func.isRequired,
  change: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  form: PropTypes.string.isRequired,
  storeForm: PropTypes.object,
  motorFields: PropTypes.object.isRequired,
  closePopUp: PropTypes.func,
  setSubmit: PropTypes.func.isRequired,
  setInsurFormState: PropTypes.func.isRequired,
  isPopUp: PropTypes.bool,
  changeDetailForm: PropTypes.func.isRequired,
  submitCallback: PropTypes.func,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  storeForm: getFormValues(formName)(state),
  isRevert: state.editForm.isRevert,
  constants: state.insurance.constants,
});

const mapDispatchToProps = dispatch => ({
  setSubmit: () => {
    dispatch(invokeSubmit());
  },
  resetForm: () => {
    dispatch(reset(formName));
  },
  getMotorPlans: (plansData) => {
    dispatch(getMotorPlans(plansData));
  },
  setInsurFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  changeDetailForm: (key, value) => {
    dispatch(change('motor-detail', key, value));
  },
});

export default withRouter(reduxForm({
  form: formName,
  destroyOnUnmount: false,
})(connect(mapStateToProps, mapDispatchToProps)(MotorEditDetail)));
